"use client";
import { useState, useEffect, useRef } from "react";
import { useRouter } from "next/navigation";

function OtpInputs({ value, onChange }: { value: string; onChange: (v: string) => void }) {
  const inputs = new Array(6).fill(0);
  const refs = useRef<HTMLInputElement[]>([]);

  useEffect(() => {
    // whenever value length changes, focus next empty
    const idx = value.length;
    if (idx < 6) refs.current[idx]?.focus();
  }, [value]);

  function onInput(e: React.ChangeEvent<HTMLInputElement>, idx: number) {
    const ch = e.target.value.replace(/\D/g, "").slice(-1);
    const arr = value.split("");
    arr[idx] = ch;
    const newVal = arr.join("").slice(0, 6);
    onChange(newVal);
    if (ch && idx < 5) {
      refs.current[idx + 1]?.focus();
    }
  }

  function onKeyDown(e: React.KeyboardEvent<HTMLInputElement>, idx: number) {
    if (e.key === "Backspace" && !value[idx] && idx > 0) {
      refs.current[idx - 1]?.focus();
    }
  }

  return (
    <div className="flex gap-2 justify-center">
      {inputs.map((_, i) => (
        <input
          key={i}
          ref={(el) => {
            if (el) refs.current[i] = el;
          }}
          value={value[i] ?? ""}
          onChange={(e) => onInput(e, i)}
          onKeyDown={(e) => onKeyDown(e, i)}
          maxLength={1}
          className="w-10 h-12 text-center border rounded"
          inputMode="numeric"
        />
      ))}
    </div>
  );
}

export default function SignupPage() {
  const router = useRouter();

  const [email, setEmail] = useState("");
  const [otpSent, setOtpSent] = useState(false);
  const [otp, setOtp] = useState("");
  const [timer, setTimer] = useState(0);
  const timerRef = useRef<number | null>(null);
  const [otpVerified, setOtpVerified] = useState(false);
  const [showVerifiedMsg, setShowVerifiedMsg] = useState(false);

  // signup fields after OTP
  const [name, setName] = useState("");
  const [password, setPassword] = useState("");
  const [password2, setPassword2] = useState("");
  const [role, setRole] = useState<"viewer" | "editor" | "admin">("viewer");

  // send OTP
  async function sendOtp() {
    if (!email) return alert("Enter email");
    const res = await fetch("/api/auth/send-otp", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ email }),
    });
    const data = await res.json();
    if (res.ok) {
      setOtpSent(true);
      setTimer(60);
      // start timer
      if (timerRef.current) window.clearInterval(timerRef.current);
      timerRef.current = window.setInterval(() => {
        setTimer((t) => {
          if (t <= 1) {
            if (timerRef.current) window.clearInterval(timerRef.current);
            return 0;
          }
          return t - 1;
        });
      }, 1000);
    } else {
      alert(data.error || "Could not send OTP");
    }
  }

  async function verifyOtp() {
    if (otp.length !== 6) return alert("Enter 6-digit OTP");
    const res = await fetch("/api/auth/verify-otp", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ email, code: otp }),
    });
    const data = await res.json();
    if (res.ok) {
      setOtpVerified(true);
      setShowVerifiedMsg(true);
      setTimeout(() => {
        setShowVerifiedMsg(false);
      }, 3000); // show for 3s
    } else {
      alert(data.error || "Verification failed");
    }
  }

  async function submitSignup(e: React.FormEvent) {
    e.preventDefault();
    if (!otpVerified) return alert("Verify OTP first");
    if (!name || !password || !password2) return alert("Fill all fields");
    if (password !== password2) return alert("Passwords do not match");

    const res = await fetch("/api/auth/signup", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ email, name, password, role }),
    });
    const data = await res.json();
    if (res.ok) {
      alert("Your account was created successfully");
      router.push("/auth/login");
    } else {
      alert(data.error || "Signup failed");
    }
  }

  return (
    <div>
      {!otpSent && (
        <form onSubmit={(e) => { e.preventDefault(); sendOtp(); }} className="space-y-4">
          <div>
            <label className="block text-sm">Email</label>
            <input type="email" value={email} onChange={(e) => setEmail(e.target.value)} required className="input mt-1" />
          </div>
          <div className="flex gap-2">
            <button type="button" onClick={sendOtp} className="btn">Create account</button>
            <a href="/api/auth/signin/google" className="btn-outline">Sign up with Google</a>
          </div>
        </form>
      )}

      {otpSent && !otpVerified && (
        <div className="space-y-4">
          <p className="text-sm">Enter the 6-digit code sent to <strong>{email}</strong></p>

          <OtpInputs value={otp} onChange={setOtp} />

          <div className="flex items-center justify-between">
            <button onClick={verifyOtp} className="btn">Verify OTP</button>

            <div>
              {timer > 0 ? (
                <span className="text-sm">Resend in {timer}s</span>
              ) : (
                <button className="text-sm text-blue-600" onClick={() => { setOtp(""); sendOtp(); }}>
                  Resend
                </button>
              )}
            </div>
          </div>
        </div>
      )}

      {showVerifiedMsg && (
        <div className="text-center text-green-600">OTP verified</div>
      )}

      {otpVerified && (
        <form onSubmit={submitSignup} className="space-y-4 mt-4">
          <div>
            <label className="block text-sm">Full name</label>
            <input value={name} onChange={(e) => setName(e.target.value)} required className="input mt-1" />
          </div>

          <div>
            <label className="block text-sm">Password</label>
            <input type="password" value={password} onChange={(e) => setPassword(e.target.value)} required className="input mt-1" />
          </div>

          <div>
            <label className="block text-sm">Confirm Password</label>
            <input type="password" value={password2} onChange={(e) => setPassword2(e.target.value)} required className="input mt-1" />
          </div>

          <div>
            <label className="block text-sm mb-2">Role</label>
            <div className="flex gap-4">
              <label><input type="radio" checked={role === "viewer"} onChange={() => setRole("viewer")} /> Viewer</label>
              <label><input type="radio" checked={role === "editor"} onChange={() => setRole("editor")} /> Editor</label>
              <label><input type="radio" checked={role === "admin"} onChange={() => setRole("admin")} /> Admin</label>
            </div>
          </div>

          <button className="btn w-full" type="submit">Create account</button>
        </form>
      )}
    </div>
  );
}
